<template>
  <div class="tooltip__overlay" v-scroll-lock="isShow">
    <div
        :class="['tooltip__modal', {'talk': withTalkLink}]"
        ref="modal"
        @keydown.esc="close"
        tabindex="0"
        v-click-outside="close"
    >
      <span class="icon icon__close icon__close-blue close-icon" @click.prevent="close"></span>
      <div class="tooltip__header">
        <div class="icon">
          <img src="~@/assets/img/icon/baseline-help-24px.svg" alt="icon">
        </div>
      </div>
      <div class="tooltip__content">
        <slot name="content" />
      </div>

      <div class="tooltip__footer" v-if="withTalkLink">
        <slot name="link">
          <a href="">{{ $ml.get('tooltip.talk') }}</a>
        </slot>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Tooltip",
    props: {
      label: {
        type: String,
        default: '',
      },
      withTalkLink: {
        type: Boolean,
        default: false,
      },
    },
    data() {
      return {
        isShow: true,
      }
    },
    methods: {
      close() {
        this.isShow = false;
        this.$emit('close');
      },
    },
    mounted() {
      if(this.$refs['modal']) {
        this.$refs['modal'].focus();
      }
    },
  }
</script>

<style scoped lang="scss">
  @import "~@/assets/scss/components/tooltip.scss";
</style>