<template>
  <div :class="['family', ...classList]">
    <div class="plyr--audio">
      <div class="audio-player">
        <VuePlyr>
          <audio controls crossorigin preload="auto">
            <source v-for="(source, key) in sources" :key="key" :src="source.file" :type="source.type"/>
          </audio>
        </VuePlyr>
      </div>
    </div>
  </div>


</template>

<script>
import VuePlyr from 'vue-plyr';
import 'vue-plyr/dist/vue-plyr.css';

export default {
  name: "AudioPlayer",
  props: {
    /**
     * [
     *  {
     *    file: string,
     *    type: 'string',
     *  },
     * ]
     */
    sources: {
      type: Array,
      default() {
        return [];
      },
    },
    classList: {
      type: Array,
      default() {
        return [];
      },
    }
  },
  components: {
    VuePlyr,
  },
  async mounted() {
    const settingButtons = await document.getElementsByClassName('plyr__menu');
    if(settingButtons) {
      settingButtons.forEach((value, index) => {
        value.innerHTML = '';
        value.insertAdjacentHTML('afterbegin', `
          <span class="audio-player__menu" id="audio-player-menu">
            <svg style="width: 24px; height: 24px; padding: 0 10px 0 10px" xmlns="http://www.w3.org/2000/svg" focusable="false" width="4" height="16" viewBox="0 0 4 16">
              <path id="Path_2017" data-name="Path 2017" d="M12,8a2,2,0,1,0-2-2A2.006,2.006,0,0,0,12,8Zm0,2a2,2,0,1,0,2,2A2.006,2.006,0,0,0,12,10Zm0,6a2,2,0,1,0,2,2A2.006,2.006,0,0,0,12,16Z" transform="translate(-10 -4)" fill="#4f91b3"/>
            </svg>
          </span>
      `);
      })
    }
  },
}
</script>

<style lang="scss">
  @import "~@/assets/scss/components/audio-player.scss";

  .audio-player {
    height: 60px;
    margin: 0 auto;
    border: 1px solid #CCD7DD;
    border-radius: 10px;
  }

  .family .plyr__controls, .post .plyr__controls {
    padding: 14px 9px 10px 49px !important;
  }
</style>