<template>
  <div class="period-modal__overlay" v-scroll-lock="isShow">
    <div ref="modal" class="period-modal" @keydown.esc="close" tabindex="0">
      <div class="period-modal__header">
        <span class="icon icon__close close" @click.prevent="close"></span>

        <h1 class="period-modal__title">{{ $ml.get('story.periods.title') }}</h1>
      </div>

      <div class="period-modal__wrapper">
        <div class="period-modal__content">
          <div class="period-modal__label">
            <h2>{{ $ml.get('story.periods.label') }}</h2>
            <span class="icon icon__info info" @click="isTooltipShow = !isTooltipShow"></span>
          </div>

          <div class="period-modal__description">
            <h3>{{ $ml.get('story.periods.description') }}</h3>
          </div>

          <div class="period-modal__periods">
            <div
                class="period-modal__period"
                v-for="period in periods"
                :key="period.id"
                @click="selectedPeriod = period"
            >
              <div class="period__logo">
                <img src="~@/assets/img/icon/general-period.svg" alt="" v-if="period.isGeneral">
                <img src="~@/assets/img/icon/calendar.svg" alt=""
                     v-else-if="!period.isGeneral && null !== period.since && null !== period.till">
                <img src="~@/assets/img/icon/label.svg" alt=""
                     v-else-if="!period.isGeneral && null === period.since && null === period.till">
              </div>


              <div class="period__info">
                <span class="period__name" v-if="period.isGeneral">{{ $ml.get('story.common.form.labels.general') }}</span>
                <span class="period__name" v-else v-html="period.name"></span>
                <div class="period__date" v-if="null !== period.since && null !== period.till">
                  <span class="since">{{ period.since | getYear }}</span> - <span class="till">{{ period.till | getYear }}</span>
                </div>
              </div>

              <span :class="['radio', {'checked': period.id === selectedPeriod.id || period.isGeneral}]">
            <span class="radio__checked"></span>
          </span>
            </div>
          </div>
        </div>

        <Button
            :classList="['btn', 'btn__primary', 'period__submit']"
            @clicked="setPeriod"
            :label="$ml.get('story.periods.submit')"
        >
          <span slot="icon-right" class="icon icon__arrow-2 white btn-arrow"></span>
        </Button>
      </div>
    </div>

    <Tooltip
        v-if="isTooltipShow"
        :label="$ml.get('story.periods.help.label')"
        :withTalkLink="false"
        @close="isTooltipShow = false"
    >
      <div slot="content" class="tooltip__description">
        <span>
           {{ $ml.get('story.periods.help.text') }}
        </span>
      </div>
    </Tooltip>
  </div>
</template>

<script>
import Button from '@/components/Button';
import Tooltip from "@/components/Tooltip";

export default {
  name: "PeriodModal",
  props: {
    memorialId: {
      type: String,
    },
    defaultPeriod: {
      type: Object,
      default() {
        return {};
      }
    },
    periods: {
      type: Array,
      default() {
        return [];
      }
    },
  },
  components: {
    Button,
    Tooltip,
  },
  data() {
    return {
      isTooltipShow: false,
      isShow: true,
      selectedPeriod: {},
    };
  },
  methods: {
    close() {
      this.isShow = false;
      this.$emit('close');
    },
    setPeriod() {
      this.$emit('selectPeriod', this.selectedPeriod);
      this.isShow = false;
      this.$emit('close');
    },
  },
  mounted() {
    if (Object.keys(this.defaultPeriod).length) {
      this.selectedPeriod = this.defaultPeriod;
    }

    if(this.$refs['modal']) {
      this.$refs['modal'].focus();
    }
  },
}
</script>


<style scoped lang="scss">
@import "~@/assets/scss/components/period-modal.scss";
</style>