<template>
  <div :class="[...classList]" @click.prevent.stop="click">
    <slot name="content">
      <slot name="icon-left"></slot>
      <span @click.prevent.stop="click">{{ label }}</span>
      <slot name="icon-right"></slot>
    </slot>
  </div>
</template>

<script>
  export default {
    name: "Button",
    props: {
      classList: {
        type: [Array, String],
        default() {
          return [];
        },
      },
      label: {
        type: String,
        default: '',
      },
    },
    methods: {
      click() {
        this.$emit('clicked');
      },
    },
  }
</script>

